import React from "react";
import ReactDOM from "react-dom";
import {Link} from "react-router-dom";
class Home extends React.Component{
    render(){
        return  <>
                    <div className="div gradient-border" style={{width:"40%",left:"30%",height:"20%",top:"15%",fontSize:"100px",color:"white"}}><b>ENDLESS FUN</b></div>
                    <div className="div" style={{width:"100%",left:"0%",top:"40%",fontSize:"30px"}}>Welcome to PolyMagic Games Studio</div>
                    <Game name="Jumper 3D" image="jumper3dicon.webp" left="22%" top="55%" url="https://play.google.com/store/apps/details?id=com.PolyMagic.Jumper3D"/>
                    <Game name="Square Stacker" image="squarestackericon.webp" left="42%" top="55%" url="https://play.google.com/store/apps/details?id=com.PolyMagic.SquareStacker"/>
                    <Game name="Level Up Cubes" image="levelupcubesicon.webp" left="62%" top="55%" url="https://play.google.com/store/apps/details?id=com.PolyMagic.LevelUpCubes"/>
                </>
    }
}
class Game extends React.Component{
    constructor(props){
        super(props);
        this.state={size:"300px"}
        this.play=this.play.bind(this);
    }
    play(){
        window.location.href=this.props.url;
    }
    render(){
        return  <>
                    <img onClick={this.play} src={require("./"+this.props.image)} style={{position:"absolute",width:this.state.size,height:this.state.size,left:this.props.left,top:this.props.top,borderRadius:"10px",boxShadow:"0px 0px 15px rgba(0,0,0,0.2)",cursor:"pointer"}}/>
                    <div style={{position:"absolute",width:this.state.size,left:this.props.left,top:"calc("+this.props.top+" + "+this.state.size+" + 20px)",fontSize:"25px"}}>{this.props.name}</div>
                </>
    }
}
export {Home}
